:local div.wrap h2 {
  margin-top: 25px;
}
:local div.wrap ul.plants {
  list-style: none;
  padding: 0;
  margin: 0;
}
:local div.wrap ul.plants li {
  position: relative;
  display: inline-block;
  width: 250px;
  margin: 0 10px 10px 0;
  padding: 25px;
  background-color: white;
  border: 1px solid rgba(0, 128, 64, 0.125);
  border-radius: 10px;
}
:local div.wrap ul.plants li div.wrapEditBtn {
  position: absolute;
  top: 8px;
  right: 6px;
}
:local div.wrap ul.plants li div.wrapEditBtn button {
  color: #cdc;
}
:local div.wrap ul.plants li div.wrapEditBtn button:active {
  background-color: transparent;
  color: #f55;
}
:local div.wrap button {
  cursor: pointer;
  border: none;
  background-color: white;
  outline: none;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  text-transform: uppercase;
  letter-spacing: 0.02em;
  font-weight: 700;
}
:local div.wrap button.standard {
  border: 2px solid #f55;
  color: #f55;
  padding: 6px 10px;
  border-radius: 4px;
}
:local div.wrap button:active {
  background-color: #f55;
  color: white;
}
:local div.wrap div.wrapAddBtn {
  position: fixed;
  bottom: 50px;
  right: 50px;
  z-index: 50;
}
:local div.wrap div.wrapAddBtn button {
  width: 65px;
  height: 65px;
  border-radius: 50%;
  color: white;
  background-color: #f55;
  box-shadow: 2px 5px 15px 5px rgba(255, 0, 0, 0.15), 1px 7px 10px rgba(64, 0, 64, 0.1);
}
:local div.wrap div.wrapAddBtn button:active {
  background-color: white;
  color: #f55;
}