:local div.wrap {
  display: flex;
  align-items: center;
  margin-bottom: 25px;
}
:local div.wrap ul.nav {
  display: flex;
  align-items: center;
  padding: 0;
  list-style: none;
  margin-left: 50px;
}
:local div.wrap ul.nav li {
  text-transform: uppercase;
  font-size: 14px;
}
:local div.wrap ul.nav li + li {
  margin-left: 15px;
}
:local div.wrap ul.nav li a {
  color: black;
}
:local div.wrap ul.nav li.selected a {
  color: #f55;
  text-decoration: none;
}