:local div.wrap {
  display: flex;
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background: rgba(51, 51, 51, 0.85);
  align-items: center;
  justify-content: center;
}
:local div.wrap .panel {
  width: 750px;
  padding: 30px;
  background-color: white;
  margin-bottom: 50px;
  border-radius: 2px;
  transition: opacity 0.2s ease, background-color 0.2s ease;
}
:local div.wrap .panel .content h1:first-child {
  margin-top: 0;
  font-size: 24px;
}
:local div.wrap .panel.disabled {
  pointer-events: none;
}
:local div.wrap .panel.disabled .content {
  opacity: 0.5;
}
:local div.wrap .panel footer {
  margin-top: 30px;
  text-align: right;
}
:local div.wrap .panel footer button + button {
  margin-left: 15px;
}